html {
	scroll-behavior: smooth;
  }
.hdaoBtn{
	background:#ff565e!important;
	color: #ffffff!important;
	font-family: 'Zilla Slab', serif!important;
	font-weight: 700;
	opacity: 1;
	transition: 0.3s;
}
.hdaoBtnOutline{
	background: #ffffff;
	color: #ff565e;
	border: 1px solid #ff565e;
	font-family: 'Zilla Slab', serif!important;
	font-weight: 700;
	opacity: 1;
	transition: 0.5s;
}
.hdaoBtnOutline:hover{
	background: #ff565e;
	color: #ffffff;
	transition: 0.5s;
}
.red{
	color: #ff565e!important;
}
.blue{
	color: #001e42!important;
}
.hdaoBtn:hover{
	background:#cf4148!important;
}
.hdaoBtnContrast{
	background:#001e42!important;
	color: #ffffff!important;
	font-weight: 700;
	font-family: 'Zilla Slab', serif!important;
	opacity: 1;
	transition: 0.3s;
}
.hdaoBtnContrast:hover{
	background:#00326e!important;
}
.hdaoLink:hover{
	color: #ff565e!important;
}
.zilla{

    font-family: 'Zilla Slab', serif;

}
.dmSans{

    font-family: 'DM Sans', sans-serif;

}
.bold{
	font-weight: 700;
}
h1,h2,h3,h4,h5,h6{
	font-family: 'Zilla Slab', serif;
	font-weight: 700;
}
p{
	font-family: 'DM Sans', sans-serif;
}
.fullScreenSection{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100vh;
		text-align: center;
		scroll-snap-align: center;
	
}

